.gridCheckers {
    /* width: 100vw; */
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.someText {
    color: #ffffff;
    font-family: 'Tillana', cursive;
    font-size: 3em;
    text-align: center;
}

.centerAlign {
    display: grid;
    grid-template: repeat(8, minmax(50px, 1fr)) / repeat(8, minmax(50px, 1fr));
    outline: 0.5px solid #000;
    /* outline-offset: -0.5px; */
}

.centerAlign>* {
    /* box-sizing: border-box; */
    outline: 0.5px solid #000;
    outline-offset: -0.5px;
}

.white {
    background: #ffffff;
}

.black {
    background: #383838;
}

.blackType {
    background: url('../img/black.png') center center / cover, #383838;
}

.whiteType {
    background: url('../img/white.png') center center / cover, #383838;
}

.blackQeen {
    background: url('../img/blackQeen.png') center center / cover, #383838;
}

.whiteQeen {
    background: url('../img/whiteQeen.png') center center / cover, #383838;
}

.checkerClick {
    outline: 2px solid red;
    outline-offset: -2px;
}

.mainBody {
    display: flex;
    flex-direction: row;
}

.historyAlign {
    display: flex;
    overflow: auto;
    flex-direction: column;
    width: 10vw;
    height: 28.5rem;
    /* outline: 2px solid black; */
    margin: 0 1rem;
}

.historyItem {
    background: #383838;
    color: #ffffff;
    text-align: center;
    /* width: 100%; */
    /* height: 1rem; */
    outline: 1px solid #000;
    margin: 0.2rem 0;
}

.historyItem:hover {
    background: #06b8ff;
    color: #000;
}